import React, { useEffect, useRef, useLayoutEffect, ComponentType } from 'react';
import { withRouter } from 'react-router';
import '@components/Trader/index.scss';
type AppProps = {
    match: any;
}
const Trader = (props: AppProps) => {
    const ref = useRef<HTMLHeadingElement>(null);
    const openChart = (el: HTMLElement | null, value: HTMLScriptElement) => {
        if (el) {
            el?.appendChild(value);
        }
    }
    useEffect(() => {
        if (!props?.match.params.key || !props?.match.params.stack) return;
        const key = props?.match?.params.key.toString();
        const stack = props?.match.params.stack.toString();
        const script = document.createElement('script');
        const script1 = document.createElement('script');
        script.src = 'https://s3.tradingview.com/tv.js';
        script1.innerHTML = `new TradingView.widget({
            "width": "100%",
            "height": window.innerHeight,
            "symbol": ${'"' + stack + key + '"'},
            "interval": "D",
            "timezone": "Europe/Istanbul",
            "theme": "dark",
            "style": "1",
            "locale": "tr",
            "toolbar_bg": "#f1f3f6",
            "enable_publishing": false,
            "withdateranges": true,
            "hide_top_toolbar":false,
            "hide_bottom_toolbar":false,
            "hide_side_toolbar": false,
            "details":false,
            "allow_symbol_change": true,
            "show_popup_button": false,
            "popup_width": "500",
            "popup_height": "650",
            "container_id": "tradingview_3bdcf"
        });
        document.body.style.margin = "0px";
        document.body.style.overflow = "hidden";
        document.body.style.background = "#171B26";
        `;
        console.log(script1)
        script.async = false;
        script.onload = () => {
            openChart(ref.current, script1);
        };
        if (ref !== null) {
            openChart(ref.current, script);
        }
    }, [props?.match.params.key])
    return (
        <div className="trader-container">
            <div className="container-flud trader-container-fluid">
                <div className="row">
                    <div className='col-lg-8 container-left' ref={ref}>
                        <div className="tradingview-widget-container">
                        </div>
                    </div>
                    <div className='col-lg-4 container-right'></div>
                </div>
            </div>
        </div>

    )
}

export default withRouter(Trader);